@font-face {
  font-family: 'Open Sans';
  font-weight: 400;
  font-style: normal;
  src: url('https://ux-cdn.cencosudx.io/fonts/open-sans-400.eot');
  src: url('https://ux-cdn.cencosudx.io/fonts/open-sans-400.eot?#iefix')
      format('embedded-opentype'),
    url('https://ux-cdn.cencosudx.io/fonts/open-sans-400.woff2') format('woff2'),
    url('https://ux-cdn.cencosudx.io/fonts/open-sans-400.woff') format('woff'),
    url('https://ux-cdn.cencosudx.io/fonts/open-sans-400.ttf')
      format('truetype'),
    url('https://ux-cdn.cencosudx.io/fonts/open-sans-400.svg#OpenSans')
      format('svg');
}

